
* {
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.all {

  background-image: url('/imgs/portfolio.jpg');
  background-size: 300px 150px;
  width: 100%;
  height: 1500px;

background-position: 421% 0; 
background-repeat: no-repeat;
background-size: cover; 
background-size: 90%;
}

.wrapper {
  width: 80%;
  height: auto;
  margin: 0 auto;
}
.header-wrapper {
  height: 900px;
}

.header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;

  
}

.logo h1 {
  font-size: 30pt;
  color: #ff6b56;
}


.menus-header-part {
  display: flex;
  flex-flow: row nowrap;
  width: 50%;
  justify-content: space-around;
  border: 1px solid #ccc;
  border-radius: 60px;
  height: 50px;
}

.menu-item {
  height: 100%;
  display: flex;
  align-content: center;
  align-items: center;  
}

.menus-header-part > .menu-item:hover {
  border-bottom: 2px solid #ff6b56;
}

.menu-item a {
  text-decoration: none;
  color: #58558a;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
}
.login {
  display: flex;
  width: auto;
  height: 60px;
  justify-content: space-around;
  align-items: center;
  margin-top: 15px;
  
}

.user-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: 3px solid #ff6b56;
  display: flex;
  align-content: center;
  align-items: center;
  
}
.icon_user {
  
  width: 30px;
  height: 30px;
  border-radius: 30px;
  background-color: #ff6b56;
  
  display: block;
  margin: auto;
}

.icon_user i {
  line-height: 30px;
  margin-left: 8px;
  color: #fff;
}

.login h3 {
  margin: 0 10px;
}

.slider_wrapper{
  margin-top: 15%;
}

.head_h1  {
  font-size: 20pt;
  color: #585689;
}

.head_2 {
  display: flex;
  align-content: flex-start;
}
.icon_chart-bar i{
  margin-right: 20px;
  padding: 15px;
  border: 3px solid #aea4ed;
  color: #aea4ed;
  font-size: 15pt;
  border-radius: 15px;
}

.slider_wrapper {
  width: 44%;
}

button {
  padding: 20px 50px;
    border-radius: 30px;
    margin: 20px 0;
    background-color: #fff;
    border: 1px solid salmon;
    color: #5e5990;
}

.button .learn_more {
  margin: 75px 0 50px 0;
  cursor: pointer;
}

button:hover {
  background-color: #ff6b56;
  color: #fff;
  /* transform: scale(1.1); */
  border: 1px solid #aea4ed;
}

.head_dots {
  margin: 50px 0;
  width: 38%;
  display: flex;
  justify-content: space-between;
}

.dot-circle {
  list-style-type: none;
  display: flex;
 
}

.dot-circle i {
  color: #ff6b56;
  font-size: 20pt;
}

.dot-circle:hover {
  transform: scale(1.2);
}





.SectionTwo-wrapper {
  width: 100%;
  height: 525px;
  display: flex;
}

.sectionTwo-heading {
  height: 100%;
  min-width: 380px;
}

.sectionTwo-heading-wrapper {
  height: 90%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sectionTwo-images {
  max-width: 800px;
  height: 100%;
  overflow: hidden;
  margin: auto;

  display: flex;
  flex-flow: row wrap;
}
.head_h2 {
  font-size: 22pt;
  color: #585689;
}
.small_text {
  font-size: 8pt;
  color: #ff6b56;
  letter-spacing: 5px;
  text-transform: uppercase;
}
.larger_text {
  color: rgb(90, 86, 142);
  width: 90%;
}

hr {
  /* height:3px; 
  width: 50px; 
  background-color: #ff6b56;  */
  height: 3px;
    width: 50px;
    border: none;
    background-color: #ff6b56;
    border-radius: 30px;
}

/* .image_text_wrapper {
  background-color: #ccc;
  min-width: 300px;
  min-height: 300px;
} */



.image_text {
  width: 50%;
  height: 50%;
}

.image_text img {
  height: 100%;
  width: 100%;
  margin: auto;
}
.SectionThree-wrapper {
  width: 100%;
  height: auto;
  margin-top: 150px;
}
.SectionThree-heading {
  width: 90%;
  margin: 0 auto;
}
.wrapHeading {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

.SectionThree-heading-wrapper {
  width: 330px;
}
.datetime {
  display: flex;
  width: 200px;
  justify-content: space-between;
  color: #d6d6e0;
}

.daytime {
  font-size: 100pt;
}
.monthtime {
  font-size: 35pt;
}

.section_three_banner {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.section_three_banner img {
  width: 100%;
}

.section_three_banner i {
  color: #ff6b56;
  width: 60px;
  height: 60px;

  background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 60px;
    text-align: center;
    line-height: 60px;

    font-size: 16pt;
}

.section_three_banner i:hover {
  background-color: #ff6b56;
  color: #fff;
  /* border: none; */
}

.fa-angle-left{
    margin-left: -72px;
    margin-right: 10px;
}

.fa-angle-right{
    margin-right: -72px;
    margin-left: 10px;
}
.sectionThreeBottom {
  margin: 50px 0 75px 0;
  display: flex;
  color: #5e5990;
  justify-content: space-between;

  font-size: 13pt;
  font-weight: 550;
}
.sectionThree-left {
  width: 40%;
}
.sectionThreeText {
  width: 100%;
}
.small-box-wrapper {
  display: flex;
    width: 40%;
    justify-content: space-around;
}

.small-box {
  width: 85px;
}
.small-box-heading {
  text-transform: uppercase;
  font-size: 8pt;
  margin-bottom: 20px;
  color: #ff6b56;
}

.small-box span {
  display: block;
  margin-bottom: 40px;
}

.case_study {
  margin: 20px 0;
}

.SectionFour-wrapper {
  background-color: #ff6d54;
  width: 100%;
  /* position: absolute; */
  height: 150px;
  margin: 0;
  padding: 0;
  left: 0;
}

.sectionFour-content {

    width: 80%;
    margin: 25px auto 0 auto;

    display: flex;
    justify-content: space-between;
    align-items: center;
}
.sectionFour-content h2 {
  color: #fff;
  /* width: 30%; */
}
.sectionFour-icons {
  display: flex;
  width: 40%;
  justify-content: space-around;
  align-items: center;
}

.sectionFour-icons i {
  font-size: 60pt;
  color: #fff;
}

.footer-wrapper {
  width: 100%;
  margin-top: 50px;
}
.footer {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.logo-footer {
  width: 20%;
}


.socialsWrapper {
  width: 15%;
  display: flex;
  justify-content: space-between;
}

.menus-footer-part {
  display: flex;
  flex-flow: row nowrap;
  width: 50%;
  justify-content: space-around;
  border-radius: 60px;
  height: 50px;
}
.menus-footer-part > .menu-item:hover {
  border-top: 2px solid #ff6b56;
}

.menus-footer-part > .menu-item {
  padding-top: 30px;
  box-sizing: border-box;
}

.logo-footer h1 {
  color: #ff6b56;
}
.menu-item {
  box-sizing: border-box;
}
.socialsWrapper i {
  font-size: 18pt;
}